          :min-width="100"
          v-bind="attrs"
          v-on="on"
        >
          Options
        </v-btn>
      </template>

      <v-list dense>
        <v-list-item
          :href="`https://givadispatch.com/loads/${loadInternalId}/info`"
          target="_blank"
          class="body-2 font-weight-medium"
          link
        >
          Print
        </v-list-item>
        <v-list-item
          class="body-2 font-weight-medium"
          link
