<script>
export default {
  name: 'ListItemControlsDeleted',
  props: {
    loadStatus: {
      type: String,
      required: true,
    },
    updateLoadStatus: {
      type: Function,
      required: true,
    },
    loadInternalId: {
      type: [String, Number],
      required: true,
    },
    loadRoute: {
      type: String,
      required: true,
    },
    restoreLoad: {
      type: Function,
      required: true,
    },
  },
};
</script>
