            >
              Show
            </v-btn>
          </div>
        </div>
      </div>
    </template>

    <template v-else>
      <div class="d-flex flex-wrap mb-7 px-5 loads-filters-wrapper">
        <v-container
          fluid
          pa-0
        >
          <v-row no-gutters>
            <v-col
              cols="12"
              sm="6"
              lg="2"
            >
              <v-autocomplete
                class="ma-2"
                dense
                v-model="filters.searchKey"
                outlined
                :menu-props="{ offsetY: true }"
                :disabled="isFiltersDisabled"
                :items="searchKeys"
                item-value="key"
                item-text="label"
                hide-details
                @change="handleSearchKeyChange"
              >
                <template #label>
                  <span class="d-block font-weight-medium label--text body-2">
                    Search Key
                  </span>
                </template>
              </v-autocomplete>
            </v-col>

            <v-col
              cols="12"
              sm="6"
              lg="2"
            >
              <v-text-field
                class="ma-2"
                dense
                outlined
                append-icon="mdi-magnify"
                hide-details
                clearable
                v-model="filters.searchValue"
                @input="handleSearchChange"
              >
                <template #label>
                  <span class="d-block font-weight-medium label--text body-2">
                    Search...
                  </span>
                </template>
              </v-text-field>
            </v-col>

            <v-col
              cols="12"
              sm="6"
              lg="2"
            >
              <v-autocomplete
                class="ma-2"
                :items="driversForFilters.data"
                v-model="filters.driverId"
                :disabled="isFiltersDisabled"
                item-text="full_name"
                item-value="id"
                hide-details
                clearable
                outlined
                dense
                @input="$emit('loads-filters:update', { driver: filters.driverId })"
              >
                <template #label>
                  <span class="d-block font-weight-medium label--text body-2">
                    Drivers
                  </span>
                </template>

                <template v-slot:item="{ item, parent, attrs, on }">
                  <v-list-item
                    ripple
                    v-on="on"
                    v-bind="attrs"
                    :class="item.status.toLowerCase()"
                  >
                    <v-list-item-content>
                      <v-list-item-title v-html="parent.genFilteredText(item.full_name)" />
                    </v-list-item-content>
                  </v-list-item>
                </template>
              </v-autocomplete>
            </v-col>

            <v-col
              cols="12"
              sm="6"
              lg="2"
            >
              <v-autocomplete
                class="ma-2"
                :items="dispatchersForFilters.data"
                v-model="filters.dispatcherId"
                :disabled="isFiltersDisabled"
                item-text="name"
                item-value="id"
                hide-details
                clearable
                outlined
                dense
                @input="$emit('loads-filters:update', { dispatcher_id: filters.dispatcherId })"
              >
                <template #label>
                  <span class="d-block font-weight-medium label--text body-2">
                    Dispatchers
                  </span>
                </template>

                <template v-slot:item="{ item, parent, attrs, on }">
                  <v-list-item
                    ripple
