<script>
import { createNamespacedHelpers } from 'vuex';
import { actions as dispatchersActions } from '@modules/dispatchers/constants';
import { actions as driversActions } from '@modules/drivers/constants';
import debounce from 'lodash.debounce';

const {
  mapActions: mapDriversActions,
  mapState: mapDriversState,
} = createNamespacedHelpers('drivers');

const {
  mapActions: mapDispatchersActions,
  mapState: mapDispatchersState,
} = createNamespacedHelpers('dispatchers');

const {
  mapState: mapLoadsState,
} = createNamespacedHelpers('loads');

export default {
  name: 'LoadsFilters',
  watch: {
    '$route.query': {
      immediate: true,
      handler(nextQuery) {
        if (Object.keys(nextQuery).length === 0) {
          this.clearFilters(true);
        }

        if (nextQuery.driver && nextQuery.driver !== this.filters.driverId) {
          this.filters.driverId = nextQuery.driver;
          this.filters.driverId = Number(nextQuery.driver);
        }
      },
    },
  },
  props: {
    isFiltersMobileOpen: {
      type: Boolean,
      required: true,
    },
    onFiltersClose: {
      type: Function,
      required: true,
    },
    isFiltersDisabled: {
      type: Boolean,
      required: true,
    },
  },
  data: () => ({
    filters: {
      driverId: null,
      dispatcherId: null,
      searchValue: '',
      searchKey: 'load_id',
      prevSearchKey: 'load_id',
      sortBy: null,
    },
    searchKeys: [
      { key: 'load_id', label: 'Load ID' },
      { key: 'id', label: 'Internal Load ID' },
      { key: 'invoice_id', label: 'Invoice ID ' },
      { key: 'payment_method', label: 'Payment Type' },
      { key: 'vehicle_vin', label: 'Vehicle VIN' },
      { key: 'vehicle', label: 'Vehicle' },
      { key: 'search_customer', label: 'Shipper/Customer' },
      { key: 'pickup_company_name', label: 'Pickup Name' },
      { key: 'delivery_company_name', label: 'Delivery Name' },
    ],
    sortByKeys: [
      { key: 'sort_by_customer', label: 'Shipper/Customer' },
      { key: 'pickup_city', label: 'Pickup City' },
      { key: 'delivery_city', label: 'Delivery City' },
      { key: 'pickup_state', label: 'Pickup State' },
      { key: 'delivery_state', label: 'Delivery State' },
      { key: 'pickup_date', label: 'Pickup Date' },
      { key: 'delivery_date', label: 'Delivery Date' },
      { key: 'driver', label: 'Dispatched to Driver' },
    ],
  }),
  methods: {
    handleSortByInput() {
      this.$emit('loads-filters:update', {
        [this.$route.query.sort_by]: undefined,
      });

      this.$emit('loads-filters:update', { sort_by: this.filters.sortBy });
    },
    handleSearchChange: debounce(function bounce() {
      this.$emit('loads-filters:update', {
        [this.filters.prevSearchKey]: undefined,
      });

      this.$emit('loads-filters:update', {
        [this.filters.searchKey]: this.filters.searchValue || undefined,
      });

      this.filters.prevSearchKey = this.filters.searchKey;
    }, 750),
    handleSearchKeyChange: debounce(function bounce() {
      if (!this.filters.searchValue) return;

      this.$emit('loads-filters:update', {
        [this.filters.prevSearchKey]: undefined,
      });

      this.filters.prevSearchKey = this.filters.searchKey;
      this.filters.searchValue = '';
    }, 750),
    clearFilters(resetStatus) {
      this.$emit('loads-filters:clear', resetStatus);

      this.filters.sortBy = undefined;
      this.filters.driverId = undefined;
      this.filters.dispatcherId = undefined;
      this.filters.searchValue = undefined;
      this.filters.searchKey = 'load_id';
      this.filters.prevSearchKey = 'load_id';
    },
    ...mapDriversActions({
      getDrivers: driversActions.GET_DRIVERS_FOR_FILTERS,
    }),
    ...mapDispatchersActions({
      getDispatchers: dispatchersActions.GET_DISPATCHERS_FOR_FILTERS,
    }),
  },
  computed: {
    ...mapLoadsState(['sort']),
    ...mapDriversState(['driversForFilters']),
    ...mapDispatchersState(['dispatchersForFilters']),
  },
  mounted() {
    this.getDrivers({ query: '?status=active,invited,deactivated' });
    this.getDispatchers({ query: '?status=active,deactivated' });

    this.filters.driverId = Number(this.$route.query.driver);
    this.filters.dispatcherId = Number(this.$route.query.dispatcher_id);

    const searchObject = this.searchKeys.find((item) => this.$route.query[item.key]);
    const sortByObject = this.sortByKeys.find((item) => this.$route.query.sort_by && this.$route.query.sort_by === item.key);

    if (searchObject) {
      this.filters.searchKey = searchObject.key;
      this.filters.searchValue = this.$route.query[searchObject.key];
      this.filters.prevSearchKey = this.filters.searchKey;
    }

    if (sortByObject) {
      this.filters.sortBy = sortByObject.key;
    }
  },
};
</script>

<style lang="scss" scoped>
.loads-filters .v-input {
  font-size: 12px;
}
