<script>
import StatusLoad from '@components/Loads/StatusLoad/StatusLoad.vue';
import ListItemControls from '@modules/loads/components/LoadsListItem/components/ListItemControls';
import formatDate from '@utils/filters/formatDate';

import slagify from 'slugify';

export default {
  name: 'LoadsListItem',
  components: {
    StatusLoad,
    ListItemControls,
  },
  filters: {
    formatDate,
  },
  props: {
    load: {
      type: Object,
      required: true,
    },
    isSordByFiltersOpened: {
      type: Boolean,
      required: true,
    },
  },
  computed: {
    computedLoadRoute() {
      return slagify(this.load.load_id, {
        lower: true,
      });
    },
    computedLoadStatus() {
      return slagify(this.load.status, {
        lower: true,
      });
    },
  },
  methods: {
    getPhones(phonesString) {
      return phonesString.split(',');
    },
  },
};
</script>

