<template>
  <component
    :is="computedControlsComponent"
    :load-route="loadRoute"
    :load-status="status"
    :load-internal-id="loadInternalId"
    :restore-load="restoreLoad"
    :update-load-status="updateStatus"
    :undispatch-from-driver="undispatchFromDriver"
  />
</template>

<script>
import { createNamespacedHelpers } from 'vuex';
import { actions } from '@modules/loads/constants';
import ListItemControlsArchived from '@modules/loads/components/LoadsListItem/components/ListItemControlsArchived';
import ListItemControlsNew from '@modules/loads/components/LoadsListItem/components/ListItemControlsNew';
import ListItemControlsDeleted from '@modules/loads/components/LoadsListItem/components/ListItemControlsDeleted';
import ListItemControlsPaid from '@modules/loads/components/LoadsListItem/components/ListItemControlsPaid';
import ListItemControlsPastDue from '@modules/loads/components/LoadsListItem/components/ListItemControlsPastDue';
import ListItemControlsBilled from '@modules/loads/components/LoadsListItem/components/ListItemControlsBilled';
import ListItemControlsDelivered from '@modules/loads/components/LoadsListItem/components/ListItemControlsDelivered';
import ListItemControlsPickedUp from '@modules/loads/components/LoadsListItem/components/ListItemControlsPickedUp';
import ListItemControlsDispatched from '@modules/loads/components/LoadsListItem/components/ListItemControlsDispatched';

const { mapActions } = createNamespacedHelpers('loads');

export default {
  name: 'ListItemControls',
  components: {
    ListItemControlsNew,
    ListItemControlsArchived,
    ListItemControlsDeleted,
    ListItemControlsPaid,
    ListItemControlsPastDue,
    ListItemControlsBilled,
    ListItemControlsDelivered,
    ListItemControlsPickedUp,
    ListItemControlsDispatched,
  },
  props: {
    status: {
      type: String,
      required: true,
    },
    loadRoute: {
      type: String,
      required: true,
    },
    loadInternalId: {
      type: [String, Number],
      required: true,
    },
  },
  computed: {
    computedControlsComponent() {
      return `list-item-controls-${this.status}`;
    },
  },
  methods: {
    updateStatus(nextStatus, prevStatus) {
      const formattedPrevStatus = prevStatus.replaceAll('-', '_');
      this.updateLoadStatus({ loadId: this.loadRoute, prevStatus: formattedPrevStatus, nextStatus })
        .then(() => this.$notificationSuccess('Load status was updated'))
        .catch((error) => {
          if (error.response.status === 400) {
            this.$notificationError(error.response.data[0]);
          } else {
            this.$notificationError('Load data wasn\'t updated');
          }
        });
    },
    undispatchFromDriver({ loadId }) {
      const formattedPrevStatus = this.status.replaceAll('-', '_');
      this.undispatchLoadFromDriver({ loadId, loadStatus: formattedPrevStatus })
        .then(() => this.$notificationSuccess(`Load ${loadId} was undispatched`))
        .catch(() => this.$notificationError('Load wasn\'t undispatched'));
    },
    ...mapActions({
      getLoads: actions.GET_LOADS,
      restoreLoad: actions.RESTORE_LOAD,
      updateLoadStatus: actions.UPDATE_LOAD_STATUS,
      undispatchLoadFromDriver: actions.UNDISPATCH_LOAD_FROM_DRIVER,
    }),
  },
};
</script>
