<template>
  <base-layout no-shadow>
    <template #header>
      <div class="d-flex justify-space-between align-center full-width">
        <h1 class="main-heading">
          Loads
        </h1>
        <v-btn
          :to="{ name: $constants.ROUTE_NAMES.LOAD_CREATE }"
          depressed
          color="danger"
          class="custom__btn white--text"
          :min-width="130"
        >
          Create Load

          <v-icon
            right
          >
            mdi-pencil
          </v-icon>
        </v-btn>
      </div>
    </template>

    <template #content>
      <div
        class="top d-flex flex-column white"
        :class="[{
          'sticky-desktop-2': !$vuetify.breakpoint.mobile,
        }]"
      >
        <loads-filters
          :is-filters-disabled="loads.loading"
          :is-filters-mobile-open="isFiltersMobileOpen"
          :on-filters-close="onFiltersClose"
          @loads-filters:open="onFiltersOpen"
          @loads-filters:update="onFiltersUpdate"
          @loads-filters:clear="onFiltersClear"
        />

        <div class="d-flex px-7">
          <loads-tabs-control-buttons
            :tabs-controls-data="computedTabsControlsData"
            @loads-tabs-controls:change-tab="handleLoadsTabsChange"
          />
        </div>
      </div>

      <v-container
        fluid
        py-sm-8
        px-sm-7
        flex-grow-1
        relative
      >
        <v-row
          class="full-size"
          no-gutters
        >
          <v-col
            class="relative"
            order="2"
            order-lg="1"
            cols="12"
            :lg="isSordByFiltersOpened ? 9 : 12"
          >
            <div
              class="flex-grow-1"
              :class="{ 'loads-loader': loads.loading }"
            >
              <template v-if="loads.loading">
                <div class="relative d-flex flex-grow-1">
                  <v-container
                    class="full-size"
                    fluid
                    d-flex
                    flex-grow-1
                    justify-center
                    align-center
                  >
                    <v-progress-circular
                      class="full-width"
                      :size="60"
                      :width="6"
                      style="width: 100%;"
                      color="primary-dark"
                      indeterminate
                    />
                  </v-container>
                </div>
              </template>
              <template v-else>
                <loads-list
                  v-if="mappedLoadsData"
                  :loads="mappedLoadsData"
                  :is-sord-by-filters-opened="isSordByFiltersOpened"
                />
                <template v-if="mappedLoadsData && !mappedLoadsData.length && !loads.loading">
                  <base-no-list-message>
                    You don't have any loads yet
                  </base-no-list-message>
                </template>
                <v-pagination
                  v-if="computedLoadsPagesCount > 1"
                  :length="computedLoadsPagesCount"
                  v-model="activePage"
                  circle
                />
              </template>
            </div>
          </v-col>
          <v-col
            v-if="isSordByFiltersOpened"
            order="1"
            order-lg="2"
            cols="12"
            lg="3"
          >
            <div class="ml-lg-4 flex-shrink-0 sorb-by-filters-wrapper">
              <div class="d-flex flex-column loads-item white pb-3">
                <v-text-field
                  v-model="sortByFilter"
                  class="pa-2 pt-4"
                  dense
                  outlined
                  append-icon="mdi-magnify"
                  clearable
                  hide-details
                >
                  <template #label>
                    <span class="font-weight-medium label--text body-2 sorb-by-filters-label">
                      {{ sortByPlaceholder }}
                    </span>
                  </template>
                </v-text-field>
                <div class="sorb-by-filters">
                  <template v-if="sort.loading">
                    <div class="pt-2">
                      <v-progress-circular
                        class="full-width"
                        :size="30"
                        :width="3"
                        style="width: 100%;"
                        color="primary-dark"
                        indeterminate
                      />
                    </div>
                  </template>
                  <v-list-item-group
                    v-model="sortItemActive"
                    v-else
                    dense
                    class="pt-2"
                    color="primary"
                  >
                    <v-list-item
                      v-for="sortItem in filteredSortItems"
                      :key="sortItem.value"
                      class="body-2"
                      @click="() => handleSortItemClick(sortItem)"
                    >
                      <v-list-item-content>{{ sortItem.value_name || sortItem.value }}</v-list-item-content>
                      <v-list-item-action>
                        <v-chip
                          label
                          small
                          pill
                          class="ml-2"
                          text-color="primary"
                          color="tab-ship-background-active"
                        >
                          <span class="font-weight-medium">{{ sortItem.total }}</span>
                        </v-chip>
                      </v-list-item-action>
                    </v-list-item>
                  </v-list-item-group>
                </div>
              </div>
            </div>
          </v-col>
        </v-row>
      </v-container>
    </template>
  </base-layout>
</template>

<script>
import { createNamespacedHelpers } from 'vuex';
import LoadsList from '@modules/loads/components/LoadsList';
import BaseNoListMessage from '@components/BaseNoListMessage';
import LoadsTabsControlButtons from '@modules/loads/components/LoadsTabsControlButtons';
import LoadsFilters from '@modules/loads/components/LoadsFilters';
import BaseLayout from '@components/BaseLayout';
import {
  actions as loadsActions,
  mutations as loadsMutations,
  LOAD_STATUS_KEYS_IN_ORDER,
} from '@modules/loads/constants';
import { actions as dictionariesActions } from '@/store/dictionaries/constants';
import * as R from 'ramda';

const {
  mapMutations: mapLoadsMutations,
  mapActions: mapLoadsActions,
  mapGetters: mapLoadsGetters,
  mapState: mapLoadsState,
} = createNamespacedHelpers('loads');
const {
  mapActions: mapDictionariesActions,
  mapState: mapDictionariesState,
} = createNamespacedHelpers('dictionaries');

export default {
  name: 'Loads',
  components: {
    BaseLayout,
    LoadsList,
    LoadsFilters,
    LoadsTabsControlButtons,
    BaseNoListMessage,
  },
  watch: {
    'sort.data': {
      handler(next, prev) {
        if (!prev.length && next.length && this.$route.query[this.$route.query.sort_by]) {
          this.sortItemActive = this.filteredSortItems.findIndex(({ value }) => (
            String(value) === String(this.$route.query[this.$route.query.sort_by])
          ));
        }
      },
      deep: true,
    },
    '$route.query': {
      handler(nextQuery, prevQuery) {
        if (Object.keys(nextQuery).length === 0) return;
        this.updateLoadsQuery(nextQuery);
        const isFiltersReset = R.equals(nextQuery, { page: '1', status: 'new' });
        const isSotrByUnselected = prevQuery.sort_by && !nextQuery.sort_by;

        if (nextQuery.sort_by === prevQuery.sort_by || isFiltersReset || isSotrByUnselected) {
          this.getLoads({ query: this.queryString });
        }

        if (nextQuery.sort_by === undefined) {
          this.getLoadsFiltersSortBy({ key: null });
          this.sortItemActive = null;
        } else if (prevQuery && nextQuery.sort_by && prevQuery.sort_by !== nextQuery.sort_by) {
          this.getLoadsFiltersSortBy({ key: nextQuery.sort_by });
          this.sortByPlaceholder = this.sortByKeys.find((item) => item.key === nextQuery.sort_by).label;
        } else if (!prevQuery && nextQuery.sort_by) {
          this.getLoadsFiltersSortBy({ key: nextQuery.sort_by });
          this.sortByPlaceholder = this.sortByKeys.find((item) => item.key === nextQuery.sort_by).label;
        }
      },
    },
  },
  data() {
    return {
      isFiltersMobileOpen: false,
      isDispatchDrawerOpened: false,
      isDispatchDrawerLoadId: null,
      sortByFilter: '',
      sortByPlaceholder: '',
      sortItemActive: null,
      sortByKeys: [
        { key: 'sort_by_customer', label: 'Customer' },
        { key: 'pickup_city', label: 'Pickup City' },
        { key: 'delivery_city', label: 'Delivery City' },
        { key: 'pickup_state', label: 'Pickup State' },
        { key: 'delivery_state', label: 'Delivery State' },
        { key: 'pickup_date', label: 'Pickup Date' },
        { key: 'delivery_date', label: 'Delivery Date' },
        { key: 'driver', label: 'Dispatched to Driver' },
      ],
    };
  },
  mounted() {
    this.updateLoadsQuery(this.$route.query);
    this.getLoadsFiltersSortBy({ key: this.$route.query.sort_by });
    this.getLoads({ query: this.queryString });
    this.getVehicles();
  },
  computed: {
    isSordByFiltersOpened() {
      return Boolean(this.$route.query.sort_by);
    },
    filteredSortItems() {
      if (this.sortByFilter) {
        return this.sort.data.filter((item) => {
          if (item.value_name) {
            return item.value_name.includes(this.sortByFilter);
          }

          return item.value.includes(this.sortByFilter);
        });
      }

      return this.sort.data;
    },
    activePage: {
      get() {
        return Number(this.query.page);
      },
      set(page) {
        this.updateQuery({ page });
      },
    },
    computedTabsControlsData() {
      return R.map((status) => ({
        ...this.loads.data[status],
        records: Boolean(this.loads.data[status]?.records),
      }), LOAD_STATUS_KEYS_IN_ORDER);
    },
    computedLoadsData() {
      return R.prop('records', R.head(R.filter(({ records }) => records, Object.values(this.loads.data))));
    },
    computedLoadsPagesCount() {
      return R.prop('pages', R.head(R.filter(({ records }) => records, Object.values(this.loads.data))));
    },
    mappedLoadsData() {
      return this.computedLoadsData && this.computedLoadsData.length
        ? R.map((load) => ({
          ...load,
          vehicles: R.map((vehicle) => ({
            ...vehicle,
            type: R.prop('name', R.head(R.filter(({ id }) => vehicle.type === id, this.vehicles.data))),
          }), load.vehicles),
        }), this.computedLoadsData) : [];
    },
    ...mapLoadsState(['loads', 'query', 'sort']),
    ...mapLoadsGetters(['status', 'queryString']),
    ...mapDictionariesState(['vehicles']),
  },
  methods: {
    handleSortItemClick(sortItem) {
      this.updateQuery({ [this.$route.query.sort_by]: sortItem.value });
    },
    updateQuery(nextQuery) {
      this.updateRouteQuery({ nextQuery, vm: this });
    },
    onFiltersOpen() {
      this.isFiltersMobileOpen = true;
      this.$nextTick(() => {
        document.querySelector('html').classList.add('filters-opened');
      });
    },
    onFiltersClose() {
      this.isFiltersMobileOpen = false;
      this.$nextTick(() => {
        document.querySelector('html').classList.remove('filters-opened');
      });
    },
    onFiltersUpdate(query) {
      if (query && query.sort_by) {
        this.sortItemActive = null;
      }

      this.updateQuery({ ...query, page: 1 });
    },
    onFiltersClear(resetStatus) {
      if (!resetStatus) {
        this.clearRouteQuery({ vm: this, status: this.query.status });
        this.resetLoadQuery({ status: this.query.status });
      } else {
        this.clearRouteQuery({ vm: this, status: 'new' });
        this.resetLoadQuery({ status: 'new' });
        this.updateQuery({ status: 'new', page: 1 });
      }
    },
    handleLoadsTabsChange(status) {
      this.updateQuery({ status, page: 1 });
    },
    ...mapLoadsActions({
      getLoadsFiltersSortBy: loadsActions.GET_LOADS_FILTERS_SORT_BY,
      getLoads: loadsActions.GET_LOADS,
      updateRouteQuery: loadsActions.UPDATE_ROUTE_QUERY,
      clearRouteQuery: loadsActions.CLEAR_ROUTE_QUERY,
    }),
    ...mapLoadsMutations({
      updateLoadsQuery: loadsMutations.UPDATE_LOADS_QUERY,
      resetLoadQuery: loadsMutations.RESET_LOAD_QUERY,
    }),
    ...mapDictionariesActions({
      getVehicles: dictionariesActions.GET_VEHICLES,
    }),
  },
};
</script>

