<template>
  <v-tabs
    show-arrows
    :center-active="$vuetify.breakpoint.xsOnly"
    class="tab__group"
    v-model="active"
  >
    <v-tabs-slider color="primary" />
    <v-tab
      v-for="({ title, count, records }, index) in tabsControlsData"
      :key="title"
      class="tab__control"
      @click="() => handleClick(index)"
    >
      {{ title }}
      <v-chip
        label
        small
        pill
        class="chips"
        :text-color="records ? 'primary' : 'label'"
        :color="records ? 'tab-ship-background-active' : 'tab-ship-background'"
      >
        {{ count }}
      </v-chip>
    </v-tab>
  </v-tabs>
</template>

<script>
import * as constants from '@modules/loads/constants';

export default {
  name: 'LoadsTabsControlButtons',
  props: {
    tabsControlsData: {
      type: Array,
      required: true,
    },
  },
  computed: {
    active: {
      get() {
        return this.getItemIndexWithRecords();
      },
      set() {
        return this.getItemIndexWithRecords();
      },
    },
  },
  methods: {
    handleClick(index) {
      this.$emit('loads-tabs-controls:change-tab', constants.LOAD_STATUS_KEYS_IN_ORDER[index]);
    },
    getItemIndexWithRecords() {
      return this.tabsControlsData.findIndex((item) => item.records);
    },
  },
};
</script>
